export const skillsImage = (skill) => {
    const skillID = skill.toLowerCase();
    switch (skillID) {
        case 'html':
            return html;
        case 'adobe xd':
            return adobeXd;
        case 'css':
            return css;
        case 'javascript':
            return javascript;
        case 'react':
            return react;
        case 'vue':
            return vue;
        case 'mongodb':
            return mongoDB;
        case 'mysql':
            return mysql;
        case 'c':
            return c;
        case 'c++':
            return cplusplus;
        case 'java':
            return java;
        case 'matlab':
            return matlab;
        case 'python':
            return python;
        case 'numpy':
            return numpy;
        case 'pytorch':
            return pytorch;
        case 'webix':
            return webix;
        case 'wordpress':
            return wordpress;
        case 'figma':
            return figma;
        case 'microsoft office':
            return microsoftoffice;
        default:
            break;
    }
}